import React from "react";
import Chart from "react-apexcharts";

import Container from "react-bootstrap/Container";

export default function ApexBarChartsGrouped(props) {

    console.log(props);

    let defaultApexBarChartsGroupedOptions = {
        chart: {
            type: 'bar',
        },
        plotOptions: {
            bar: {
                //       horizontal: true,
                dataLabels: {
                    position: 'top',
                },
            }
        },
        dataLabels: {
            enabled: true,
            offsetX: -6,
            style: {
                fontSize: '12px',
                colors: ['#fff']
            }
        },
        stroke: {
            show: true,
            width: 1,
            colors: ['#fff']
        },
        xaxis: {
          categories: [],
        },
    }

    let options = (() => {
        if (!!props?.options) {
            let updatedOptions = { ...defaultApexBarChartsGroupedOptions, ...props.options };
            return updatedOptions;
        };
        return defaultApexBarChartsGroupedOptions
    })();

    let categories = (() => {
        if (!!props?.categories) { return props.categories };
        return [2001, 2002, 2003, 2004, 2005, 2006, 2007]
    })();

    options.xaxis.categories = categories;

    let series = (() => {
        if (!!props?.series) { return props.series };
        return [{
            name: 'Кэш фло',
            data: [44, 55, 41, 64, 22, 43, 21],
        }, {
            name: "Дисконтированный кэш-фло",
            data: [44, 50, 33, 52, 13, 31, 14]
        }];
    })();

   
    return <Container className="mt-3" >
        <Chart options={options} series={series} type="bar" />
    </Container>

}

